
import { Node, Camera } from "cc";
import { LayerType, PopUpType } from "../../common/enum/ui-layer.enum";
import { enumEntries } from '../../common/enum/utils';
import { Fwk } from "../../Fwks";

export class UIMgr {
    /** UI 根节点 */
    uiRoot!: Node;
    /** UI 摄像机 */
    uiCamera!: Camera

    constructor() {
    }

    /** 
     * 初始化 UI节点
     * @param uiRoot UI 根节点
     */
    initLayer(uiRoot: Node): void {

        // 按枚举值升序排序后遍历
        const layerEntries = enumEntries(LayerType).sort((a, b) => Number(a[1]) - Number(b[1]));
        for (const [layerName, layerType] of layerEntries) {
            Fwk.log.trace(`UIMgr init layer: ${layerName} (${layerType})`);
        }

        const popupEntries = enumEntries(PopUpType).sort((a, b) => Number(a[1]) - Number(b[1]));
        for (const [popUpName, popUpType] of popupEntries) {
            Fwk.log.trace(`UIMgr init popup type: ${popUpName} (${popUpType})`);
        }
    }

}
